    var data = this.state.selected || [];
    var daftarArray = [];
    data.forEach((data, index) => {
      var hasil = {
        user_id: this.state.userIdSelected,
        menu_id: data,
        menu_name: data,
      };
      daftarArray.push(hasil);
    });
